body {
    background-color: white
}

/* deeplink page styles*/
.paper {
    overflow: hidden;
    width: 100%;
    display: flex;
    justify-content: center
}

.fab {
    margin: 20px;
}

.btnDiv {
    margin-top: 10px;
    display: flex;
    justify-content: center;
}

Grid {
    margin-top: 5px;
}

.controls {
    margin-top: 5px;
    padding: 7px;
    padding-right: 25px;
    box-shadow: 3px 3px 1px lightGrey;
    /*border: 1px black solid;*/
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.widthSlider {
    transform: rotate(90deg);
}

.extendedIcon {
    transform: rotate(90deg);
}

