.inputLabels {
    margin-left: 10px;
    margin-bottom: 35px;
    font-size: 18px;
    font-weight: 100;
}

.previewText {
    margin-top: 20px;
    font-size: 20px;
    font-weight: 100;
}

/* colour picker styles*/
.picker {
    position: relative;
    margin-left: 10px;
    z-index: 10;
}

.swatch {
    width: 28px;
    height: 28px;
    border-radius: 8px;
    border: 3px solid #bbb;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

/* Small */
.small .react-colorful {
    width: 250px;
    height: 150px;
}

.small .react-colorful__hue {
    height: 20px;
}

.picker__swatches {
    display: flex;
    padding: 12px;
    flex-wrap: wrap;
}

.picker__swatch {
    width: 24px;
    height: 24px;
    margin: 4px;
    border: none;
    padding: 0;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
    box-shadow: 3px 3px 1px lightGrey;
}

.popover {
    position: absolute;
    top: calc(100% + 2px);
    left: 0;
    border-radius: 9px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
    background-color: white;
}